<template>
    <v-container fluid grid-list-md>
       <v-row align="center"
      justify="center">
            <v-col>
                <div class="token-code px-20px mb-10px">
                    <span class="title">Token: </span>
                    <span class="text-red">{{ token }}</span>
                    <v-btn  class="copy-btn" small color="primary"  @click.stop.prevent="copyToken">Copy</v-btn>
                    <input type="hidden" id="token-code" :value="token">
                </div>
            </v-col>
        </v-row>
        <v-row v-for="message in messages" :key="message.time">
          <v-col>
         <v-card >
            <v-card-title>
               {{ message.time }}
              </v-card-title> 
            <v-card-text style="white-space: pre">
              {{ JSON.stringify(message.message, null, '\t') }}
            </v-card-text>
          </v-card>
          </v-col>
        </v-row>
    </v-container>
</template>

<script>
  import { mapState } from 'vuex'
  export default {
    name: 'HelloWorld',
    computed: {
      ...mapState({
          token: state => state.fcm_meta.token,
          messages: state => state.fcm_messages.messages 
      }) 
    },
    watch: {
      token(val) {
        console.log(val);
      },
      messages(val) {
        val.forEach(message => console.log(message))
      }
    }, 
    methods: {
      copyToken () {
        let tokenToCopy = document.querySelector('#token-code')
        tokenToCopy.setAttribute('type', 'text')    // 不是 hidden 才能複製
        tokenToCopy.select()

        try {
          var successful = document.execCommand('copy');
          var msg = successful ? 'successful' : 'unsuccessful';
          alert('Testing code was copied ' + msg);
        } catch (err) {
          alert('Oops, unable to copy');
        }

        /* unselect the range */
        tokenToCopy.setAttribute('type', 'hidden')
        window.getSelection().removeAllRanges()
      },
      replacer(key, value) {
        if (typeof(value) === 'object') {
        for (var k in value) {
            if (value[k] instanceof Date) {
                value[k] = undefined
            }
        }
    }
        return value;
      }
    }
  }
</script>

