<template>
  <v-app>
    <v-app-bar
      app
      color="primary"
      dark
    >
      <div class="d-flex align-center">
        <v-img
          alt="Mosaic Logo"
          class="mr-2"
          contain
          :src="logoPath"
          transition="scale-transition"
          width="100"
        />

        <h1>Firebase-CloudMsg-Webapp</h1>
      </div>

      <v-spacer></v-spacer>

      <v-btn
        href="https://git.hochbahn.cloud/hvv-switch/beam/firebase-cloudmsg-adapter"
        target="_blank"
        text
      >
        <span class="mr-2">API-Beschreibung</span>
        <v-icon>mdi-open-in-new</v-icon>
      </v-btn>
    </v-app-bar>

    <v-main>
      <HelloWorld/>
    </v-main>
  </v-app>
</template>

<script>
import HelloWorld from './components/HelloWorld';

export default {
  name: 'App',
  components: {
    HelloWorld
  },
  data() {
    return {
      logoPath: require('@/assets/mosaic.png')
    }
  },
  mounted () {
    console.log('Firebase cloud messaging object', this.$messaging)
    this.$messaging.getToken({ vapidKey: "BA8PWWxjd0-pkczRiW8_YyZdppsoMSWYK2zWO8tSDcy9uN19Ru4lcEdlslNH1gpJ4_hLa1ssrkMlx5Z-lg5gj1Y" })
    .then((currentToken) => {
      if (currentToken) {
        console.log('client token', currentToken)
        this.$store.dispatch('fcm_meta/tokenAction', currentToken)
        this.$messaging.onMessage(payload => {
          var new_payload = {
            time: new Date(),
            message: payload
          }
          this.$store.dispatch('fcm_messages/messageAction', new_payload)
        })
      } else {
        console.log('No registration token available. Request permission to generate one.');
      }
    }).catch((err) => {
      console.log('An error occurred while retrieving token. ', err);
    })
  }
}
</script>