const state = {
  token: ''
}

const getters = {
  token: (state) => state.token,
}

const actions = {
  tokenAction({ commit },token) {
      commit('setToken',token)
  }
}

const mutations = {
  setToken: (state, token) => state.token = token
}

export default {
  namespaced: true,
  state,
  getters,
  actions,
  mutations
}